                                    /* Aspect ratio */
                                    
                                    .ar-1-1 .panel,
                                    .ar-2-1 .panel,
                                    .ar-1-2 .panel,
                                    .ar-3-2 .panel,
                                    .ar-2-3 .panel {
                                        margin: 0;
                                    }
                                    
                                    .ar-1-1 {
                                        position: relative;
                                        width: 100%;
                                        /* desired width */
                                        overflow: hidden;
                                    }
                                    
                                    .ar-1-1:before {
                                        content: "";
                                        display: block;
                                        padding-top: 100%;
                                        /* initial ratio of 1:1*/
                                    }
                                    
                                    .ar-1-1>div {
                                        position: absolute;
                                        top: 0;
                                        left: 0;
                                        bottom: 0;
                                        right: 0;
                                    }
                                    
                                    .ar-2-1 {
                                        position: relative;
                                        width: 100%;
                                        /* desired width */
                                        overflow: hidden;
                                    }
                                    
                                    .ar-2-1:before {
                                        content: "";
                                        display: block;
                                        padding-top: calc(50% - 5px);
                                        /* initial ratio of 1:1*/
                                    }
                                    
                                    .ar-2-1>div {
                                        position: absolute;
                                        top: 0;
                                        left: 0;
                                        bottom: 0;
                                        right: 0;
                                    }
                                    
                                    .ar-1-2 {
                                        position: relative;
                                        width: 100%;
                                        /* desired width */
                                        overflow: hidden;
                                    }
                                    
                                    .ar-1-2:before {
                                        content: "";
                                        display: block;
                                        padding-top: calc(150% - 5px);
                                        /* initial ratio of 1:1*/
                                    }
                                    
                                    .ar-1-2>div {
                                        position: absolute;
                                        top: 0;
                                        left: 0;
                                        bottom: 0;
                                        right: 0;
                                    }
                                    
                                    .ar-3-2 {
                                        position: relative;
                                        width: 100%;
                                        /* desired width */
                                        overflow: hidden;
                                    }
                                    
                                    .ar-3-2:before {
                                        content: "";
                                        display: block;
                                        padding-top: calc(75% - 5px);
                                        /* initial ratio of 1:1*/
                                    }
                                    
                                    .ar-3-2>div {
